const calculateIngest = () => {
  const equivalentsContainers = document.querySelectorAll(".water-equivalent");
  let total = 0;

  equivalentsContainers.forEach((equivalentsContainers) => {
    const value =
      equivalentsContainers.querySelector(".equivalent-value").textContent;
    total += parseInt(value);
  });

  return total;
};

export default calculateIngest;
